@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&subset=cyrillic,cyrillic-ext,greek,greek-ext,latin-ext,vietnamese');

html body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans",sans-serif;
  font-size: 14px;
}

.custom-component-ticket .error-msg{
    color: red;
    padding: 5px 5px;
    text-align: center;
    display: block;
    /* background-color: rgba(242, 222, 222, 0.65); */
}

/* instructor list box */
.list-box-wrapper {
    /* background: #fff;
    box-shadow: 1px 3px 7px 1px rgba(0,0,0,0.5); */
    max-height: 465px;
    overflow-y: auto;
}

.instructor-form .error-msg {
    margin-left: 321px;
    text-align: left;
    padding: 0;
}
.list-header:after {
    content: '';
    clear: both;
    display: block;
}

.instructor-count {
    width: 25%;
    float: left;
    text-align: left;
    padding: 10px 20px;
    font-weight: bold;
}
.candidate-count{
    width: 100%;
}

.instructor-create-btn {
    width: 65%;
    float: left;
    padding: 10px 20px;
    text-align: right;
}
.custom-component-ticket .instructor-create-btn {
    width: 75%;
}
.instructor-create-btn img {
    margin-right: 10px;
    width: 20px;
    vertical-align: middle;
}
.list-header {
    border-bottom: 2px solid #ddd;
    padding: 9px 0;
    background: #fff;
}
.instructor-create-btn a {
    color: #000;
}
.list-box {
    margin-bottom: 20px;
    background: #fff;
    -webkit-box-shadow: 1px 3px 7px 1px rgba(0,0,0,.5);
    box-shadow: 1px 3px 7px 1px rgba(0,0,0,.5);
}
.list-box.grayish .view-details {
    background: #fff;
}
.list-box.grayish .view-details a:hover{
    color: #23527c;
    opacity: 0.7;
}
.list-box-wrapper .list-box:last-child {
    border: none;
}

.instructor-profile {
    width: 32%;
    float: left;
    padding: 21px 20px;
    font-weight: bold;
}

.list-box:after {
    content: '';
    display: block;
    clear: both;
}

.instructor-details {
    width: 60%;
    float: left;
    text-align: right;
    padding: 20px 20px;
}
.instructor-details a{
    color: #000;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
    width: 100%;
    display: inline-block;
    height: 100%;
    padding: 10px 20px;
}
.instructor-details a:hover{
    text-decoration: none;
    opacity: 0.7;
}
.instructor-profile span {
    display: inline-block;
    margin: 0 10px;
    vertical-align: middle;
    text-transform: capitalize;
    max-width: 230px;
}
span.instructor-img {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid #ddd;
}

span.instructor-img img {
    height: 100%;
    width: 100%;
}








