/*
=====================================================
    Site Manager Panel
=====================================================
*/


.list-header-option {
  margin: 0 15px;
}
.list-header-option img {
  cursor: pointer;
}
#sb-search{
  display: inline-block;
  width: 45%;
  margin-left: 0;
}
#search-check{
  display: none;
}
#search-field{
  width: 0px;
  -webkit-transition: width 1s ease-in-out, border 1s ease-in-out, padding 1s ease-in-out;
  -o-transition: width 1s ease-in-out, border 1s ease-in-out, padding 1s ease-in-out;
  transition: width 1s ease-in-out, border 1s ease-in-out, padding 1s ease-in-out;
  border: 1px solid transparent;
  padding: 5px 0;
  display: inline;
  border-radius: 5px;
  margin-right: -30px;
  padding-right: 30px;
}
.search-check:checked + #search-field{
  width: 100%;
  padding: 5px;
  padding-right: 30px;
  border: 1px solid #cccccc;
}
.sub-menu-parent {
  position: relative;
}
.sub-menu {
  display: none;
  opacity: 0;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  background: #fff;
  cursor: pointer;
}

.sub-menu-parent:focus .sub-menu,
.sub-menu-parent:focus-within .sub-menu,
.sub-menu-parent:hover .sub-menu {
  display: block;
  opacity: 1;
  -webkit-transform: translate(-50%, 3px);
      -ms-transform: translate(-50%, 3px);
          transform: translate(-50%, 3px);
  -webkit-transition-delay: 0s, 0s, 0.3s;
       -o-transition-delay: 0s, 0s, 0.3s;
          transition-delay: 0s, 0s, 0.3s;
  left: 50%;
}

.sub-menu-parent ul,
.sub-menu-parent ul li {
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: auto;
}
.sub-menu-parent > ul {
  background: #fff;
  text-align: left;
  padding-top: 0;
  -webkit-box-shadow: 1px 3px 15px rgba(0, 0, 0, 0.16);
          box-shadow: 1px 3px 15px rgba(0, 0, 0, 0.16);
  border-radius: 2px;
}
.sub-menu-parent > ul > li {
  display: inline-block;
  padding: 10px 15px;
  width: 100%;
  white-space: nowrap;
}
.sub-menu-parent > ul > li:hover {
  background: #eeeeee;
}




/*
======================================================
    All Media screen CSS Here..
======================================================
*/


.forget-popup-wrapper{
    z-index: 2;
}
.custom-component-ticket  .forget-popup {
    width: 775px;

}
  .sub-menu-parent:focus .sub-menu, .sub-menu-parent:focus-within .sub-menu, .sub-menu-parent:hover .sub-menu{
      overflow: hidden;
  }
  .custom-component-ticket  .forget-popup-body{
    padding: 0;
  }
  .custom-component-ticket  .forget-popup-body .body{
      overflow: hidden;
  }
  .custom-component-ticket  .forget-popup-body .body .row{
    overflow: hidden;
    border-bottom: 1px solid #eee;
    margin: 0;
  }
  .custom-component-ticket  .forget-popup-body .body .row .col-left-popup{
    float: left;
    padding: 17px;
    width: 30%;
    font-weight: normal;
  }
  .custom-component-ticket  .forget-popup-body .body .row .col-right-popup{
    float: left;
    padding: 17px;
    width: 60%;
    font-weight: bold;
    border-left: 1px solid #ddd;
    word-break: break-word;
  }
  .custom-component-ticket .forget-popup-body .body .row .col-right-popup .Select{
      position: absolute;
      margin-top: -8px;
      width: 310px;
  }
  .custom-component-ticket  .forget-popup-body .body .row .col-right-popup .Select .Select-control {
    border-radius: 20px;
    height: 36px;
    border: 1px solid #cac8c8;

}
.custom-component-ticket  .forget-popup-footer{
    text-align: center;
}

.ticket-status-col{
  padding: 7px 5px !important;
}
.ticket-status-div {
  width: 100%;
}

.ticket-status-div .ticket-status{
  float: left;
  width: 50%;
}

.ticket-status-div span {
  width: 50%;
  float: left;
  text-align: left !important;
  padding: 5px 10px;
}



